body {
    background: var(--black);
}

#loading.loading__active {
    
    opacity: 1;
    transition: all 1.6s;
}

#loading {
    opacity: 0;
    transition: all 1.6s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--black);
    z-index: 99999;
}

.loading__text {
    // 가운데 오는 효과 사방에 0주고 margin 오토
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    text-align: center;
    width: 100%;
    height: 100px;
    line-height: 100px;

    span {
        display: inline-block;
        margin: 0 5px;
        color: var(--white);
    }

    // scss반복문 사용법
    @for $i from 1 through 7 {
        span:nth-child(#{$i}){
            filter: blur(0px);
            animation: blur-text 1.5s $i * 0.2s infinite linear alternate;
        }
    }
    
