#footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: var(--white);
    border-top: 1px solid var(--dark_border);
    background-color: var(--dark_bg);
    display: flex;
    align-items: center;
    font-family: "Rajdhani";
    z-index: 1000;

    div {
        text-align: center;
        padding: 1.45rem;
    }

    h4,
    ul {
        display: inline-block;
        
    }

    h4 {
        font-weight: 600;
        padding: 0 0.6em;
        text-transform: capitalize;
        font-size: 16px;
        vertical-align: middle;
    }

    div:nth-child(1) {
        flex: 1 1 30%;
        border-right: 1px solid var(--dark_border);

        a {
            position: relative;

            &::before {
                content:'';
                position: absolute;
                left: 0;
                bottom: -0.1em;
                width: 100%;
                height: 1px;
                background: var(--white);
                transform: scaleX(0);
                transition: all 0.3s ease;
            }
    
            &:hover::before {
                transform: scaleX(1);
            }
        }
    }

    div:nth-child(2) {
        flex: 1 1 20%;
        border-right: 1px solid var(--dark_border);

        a {
            position: relative;

            &::before {
                content:'';
                position: absolute;
                left: 0;
                bottom: -0.1em;
                width: 100%;
                height: 1px;
                background: var(--white);
                transform: scaleX(0);
                transition: all 0.3s ease;
            }
    
            &:hover::before {
                transform: scaleX(1);
            }
        }
    }

    div:nth-child(3) {
        flex: 1 1 50%;

        li {
            display: inline;

            a {
                display: inline-block;
                padding: 0 0.6em;
                position: relative;

                &::before {
                    content:'';
                    position: absolute;
                    left: 0.7em;
                    bottom: 0;
                    width: calc(100% - 1.2em);
                    height: 1px;
                    background: var(--white);
                    transform: scaleX(0);
                    transition: all 0.3s ease;
                }
        
                &:hover::before {
                    transform: scaleX(1);
                }
            }
        }
    }
}

// 라이트 버젼
.light#footer {
    background-color: var(--light_bg);
    border-color: var(--light_border);

    h4 {
        color: var(--black);
    }
    
    div {
        border-right-color: #dad8d1;

        a {
            color: var(--black);
    
            &::before {
                background: var(--black);
            }
        }

        li {
            a {
                &::before {
                    background: var(--black);
                }
            }
        }
    }
}

// 미디어 쿼리
@media (max-width: 1025px){
    #footer {
        div:nth-child(1) {
            h4 {
                display: none;
            }
        }

        div:nth-child(2) {
            display: none;
        }

        div:nth-child(3) {
            display: none;
        }
    }
}

// footer Animation
#footer {
    bottom: -100%;
}